@import url("./fonts.css");

/* ==========================================================================
   Main Styles
   ========================================================================== */
html {
  height: 100%;
  width: 100%;
}

*,
*::after,
*::before {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

body,
div.elements {
  min-height: 100vh;
  flex-direction: column;
  flex: 1 0 auto;
  font-family: "Poppins", system-ui, sans-serif;
  text-rendering: optimizeLegibility;

  -moz-osx-font-smoothing: grayscale; /* Font smoothing tweaks for MacOSx in Gecko */
  -webkit-font-smoothing: antialiased;
  color: #444a59;
  background-color: #f8f8f8;
}
.elements {
  display: grid;
  justify-content: space-between;
}

.container-card {
  display: grid;
  display: -moz-grid;
  display: -ms-grid;
  display: -webkit-grid;
  grid-template-columns: 1fr auto;
  background-color: #def0f3;
  gap: 1rem;
  align-items: center;
  box-shadow: 0 5px 15px rgba(0, 0, 0, 0.1);
  padding: 0.8rem;
  border-radius: 0.25rem;
  transition: all 0.2s linear;
}
.container {
  width: 100%;
  max-width: 1240px;
  padding: 0 40px;
  margin: 0 auto;
}
.img_container {
  border: 4px solid white;
  width: 30%;
  height: 40%;
  z-index: 2;
  border-radius: 0.25rem;
}
.img {
  position: relative;
  box-shadow: 0 5px 15px rgba(0, 0, 0, 0.1);
  border-radius: 0.25rem;
  width: 100%;
  height: 100%;
  z-index: 2;
}
h1,
p,
figure {
  margin: 0;
  font-weight: normal;
}

a,
a:hover,
a:visited {
  text-decoration: none;
  color: #444a59;
}

ul,
li {
  margin: 0;
  padding: 0;
  list-style-type: none;
}

.buttons {
  text-align: center;
}
.buttons > button {
  padding: 10px;
  margin-left: 10px;
}
header,
footer {
  height: 60px;

  display: flex;
  flex-direction: row;
  align-items: center;

  text-transform: uppercase;
  background-color: #ffffff;
}

main {
  display: flex;
  flex-direction: row;
  flex: 1 0 auto;
  margin: 30px 0 20px;
}

/* ==========================================================================
   Buttons
   ========================================================================== */

.button--round {
  display: block;
  border-radius: 50%;
}
button {
  padding: 13px;
  border: 0;
  font-family: "Poppins", system-ui, sans-serif;
  font-size: 13px;
  text-transform: uppercase;
  letter-spacing: 1.39px;
}

.button--primary {
  padding: 13px;

  font-family: "Poppins", system-ui, sans-serif;
  font-size: 13px;
  text-transform: uppercase;
  letter-spacing: 1.39px;
}

.button--primary:hover {
  border: 1px solid #444a59;
  color: #ffffff;
  background-color: #444a59;
}

.button--wishlist:hover {
  border: 1px solid #444a59;
}

.button--wishlist:hover > .icon {
  fill: #444a59;
}

.button--saved {
  pointer-events: none;
  background-color: #d9d9d9;
}

/* ==========================================================================
   Header
   ========================================================================== */

.header {
  display: flex;
  flex-direction: row;
  justify-content: space-between;
}

.page-title {
  font-size: 20px;
}

/* ==========================================================================
   Favorites
   ========================================================================== */

.header-favorites {
  display: flex;
  flex-direction: row;
  align-items: flex-end;
}

.header-favorites__item {
  height: 25px;

  display: flex;
  align-items: flex-end;
}

.favorites__item-counter {
  width: 15px;
  height: 15px;
  margin-left: -10px;

  display: flex;
  align-self: flex-start;
  justify-content: center;
  align-items: center;

  font-family: "Poppins", system-ui, sans-serif;
  font-size: 9px;
  text-align: center;

  border-radius: 50%;
  color: #2a2a2a;
  background-color: #ece21c;
}

/* ==========================================================================
   Profile List
   ========================================================================== */

.profiles-list {
  display: flex;
  flex: 0 1 auto;
  flex-flow: row wrap;
  margin: 0 -10px;
}

.profiles-list__item {
  padding: 10px;
  flex: 1 0 33.3333%;
  max-width: 33.3333%;
}

/* ==========================================================================
   Profile
   ========================================================================== */

.profiles {
  display: flex;
  flex-direction: column;
  height: 100%;

  background-color: #ffffff;
}

/* ==========================================================================
   Profile Image
   ========================================================================== */

.profiles__image-wrapper {
  background-color: #e5e5e5;
  position: relative;
  text-align: center;
}

.profiles__image {
  max-width: 100%;
  max-height: 160px;
}

/* ==========================================================================
   Profile Details
   ========================================================================== */

.profiles__details {
  display: flex;
  flex: 1 0 auto;
  flex-direction: column;
  padding: 10px 20px 20px;
  text-align: center;
}

/* ==========================================================================
   Profile Titles
   ========================================================================== */

.profiles__title {
  padding-bottom: 0px;

  font-family: "Poppins", system-ui, sans-serif;
  font-size: 14px;
  letter-spacing: 1.37px;
  text-transform: uppercase;
}

.profiles__subtitle {
  padding-bottom: 25px;

  font-size: 12px;
  line-height: 19px;
  letter-spacing: 0.43px;

  color: #808080;
}

/* ==========================================================================
   Profile actions
   ========================================================================== */

.profiles__add-to-favorite {
  width: 100%;
  margin-top: 10px;
  margin-top: auto;
}

/* ==========================================================================
   Pagination
   ========================================================================== */

.pagination {
  height: 45px;
  margin: 30px auto;
  text-align: center;
}

.pagination__list {
  height: 100%;
  display: inline-flex;
  flex-direction: row;
  align-items: center;
}

.pagination__item {
  height: 100%;
  width: 45px;
  font-family: "Poppins", system-ui, sans-serif;
  font-size: 13px;
  letter-spacing: 1.39px;
  text-align: center;
}

.pagination__link {
  height: 100%;
  width: 100%;
  display: flex;
  justify-content: center;
  align-items: center;
  color: #444a59;
}

.pagination__link:hover {
  text-decoration: underline;
}

.pagination__link .icon {
  width: 8px;
  fill: #444a59;
}

/* ==========================================================================
   Footer
   ========================================================================== */

.footer__sidenote {
  font-family: "Poppins", system-ui, sans-serif;
  letter-spacing: 1.37px;
}
